import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';
import VisualScriptingDB from '../managers/VisualScriptingDB';

import logo from '../assets/images/logo192.png';

export type SettingsFormProps = {

}

export type SettingsFormState = {
    isVerifyingReset: boolean;
}

export default class SettingsForm extends React.Component<SettingsFormProps, SettingsFormState> {
    constructor(props: SettingsFormProps) {
        super(props);

        this.state = {
            isVerifyingReset: false
        }
    }

    render() {
        const { isVerifyingReset } = this.state;

        return(
            <div className="flex flex-col">
                <div className="mb-2 text-lg text-gray-800">
                    Others
                </div>
                <div className="mb-4 text-sm text-red-500">
                    Warning: This will reset the database, where your scriplets are stored, to when it was first initialized, and will thus delete every progress you have made so far. Only click on this if absolutely necessary!
                </div>
                <div className="mb-4">
                    <div className="px-4 py-2 inline rounded bg-red-500 hover:bg-red-400 text-gray-100 cursor-pointer select-none"
                            onClick={() => this.setState({isVerifyingReset: true})}>
                        <FontAwesomeIcon className="mr-3" icon="redo-alt" />
                        Reset Database
                    </div>
                </div>
                <div className={`${isVerifyingReset ? "" : "hidden"}`}>
                    <div className="mb-2 font-bold text-gray-900">
                        Are you sure?
                    </div>
                    <div className="flex flex-row">
                        <div className="px-4 py-2 mr-4 rounded bg-red-500 hover:bg-red-400 text-gray-100 cursor-pointer select-none"
                                onClick={() => {
                                    VisualScriptingDB.resetDatabase().then(() => {
                                        this.setState({isVerifyingReset: false}, () => {
                                            // This removes current page from the history!
                                            // Still works in this case because the previous page of a scriplet
                                            // may already be gone when resetting the database and going back
                                            // to it may just lead to errors/404
                                            window.location.replace("/vso");
                                        });
                                    });
                                }}>
                            <FontAwesomeIcon className="mr-3" icon="check" />
                            Yes, reset!
                        </div>
                        <div className="px-4 py-2 rounded bg-gray-600 hover:bg-gray-500 text-gray-100 cursor-pointer select-none"
                                onClick={() => this.setState({isVerifyingReset: false})}>
                            <FontAwesomeIcon className="mr-3" icon="times" />
                            No, don't!
                        </div>
                    </div>
                </div>
                <div className="mt-2 mb-2 text-lg text-gray-800">
                    About
                </div>
                <div className="flex flex-row mb-2 text-xl text-gray-900">
                    <div className="mr-4">
                        <img src={logo} alt="Logo" width={46} />
                    </div>
                    <div className="flex flex-col">
                        <div className="font-medium">
                            Visual Scripting Online
                        </div>
                        <div className="text-xs" style={{marginTop: "-5px"}}>
                            (ver. {process.env.REACT_APP_VERSION})
                        </div>
                    </div>
                </div>
                <div className="text-gray-900">
                    Made by <span className="font-medium">Mark Malabanan (Devwulf)</span> 
                </div>
                <div className="text-gray-900">
                    Made with <span className="font-medium">React JS</span>
                </div>
            </div>
        );
    }
}