import { INodeItem, NodeItem } from "./NodeItem";
import { BaseSchemaTypes } from "./NodeSchema";
import VisualScriptingDB from "../managers/VisualScriptingDB";
import { IdValuePair, ItemSlotPair } from "./NodeSlot";

export type VariableItem = {
    id: number;
    name: string;
    type: string;
    startValue: any;
    value: any;
}

export interface IScriplet {
    id?: number;
    name: string;
    description?: string;
    start?: INodeItem;
    items?: Map<number, INodeItem>;
    itemCounter?: number;
    variables?: Map<number, VariableItem>;
    variableCounter?: number;
    inputs?: Map<number, VariableItem>;
    inputCounter?: number;
    outputs?: Map<number, VariableItem>;
    outputCounter?: number;
}

export class Scriplet implements IScriplet {
    public id: number;
    public name: string;
    public description: string;
    public start: INodeItem;
    public items: Map<number, INodeItem>;
    public itemCounter: number;
    public variables: Map<number, VariableItem>;
    public variableCounter: number;
    public inputs: Map<number, VariableItem>;
    public inputCounter: number;
    public outputs: Map<number, VariableItem>;
    public outputCounter: number;

    constructor(name: string, id?: number, description?: string, start?: INodeItem, items?: Map<number, INodeItem>, itemCounter?: number, variables?: Map<number, VariableItem>, variableCounter?: number, inputs?: Map<number, VariableItem>, inputCounter?: number, outputs?: Map<number, VariableItem>, outputCounter?: number) {
        this.id = id ?? 0;
        this.itemCounter = itemCounter ?? 1;
        this.variableCounter = variableCounter ?? 1;
        this.inputCounter = inputCounter ?? 1;
        this.outputCounter = outputCounter ?? 1;

        this.name = name;
        this.description = description ?? "";
        this.start = start ?? {id: 0, x: 0, y: 0, schemaId: BaseSchemaTypes.Start, inputValues: [], inputSlots: [], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: []};
        if (items == null) {
            this.items = new Map<number, INodeItem>();
            this.items.set(0, this.start);
        } else
            this.items = items;
        
        this.variables = variables ?? new Map<number, VariableItem>();
        this.inputs = inputs ?? new Map<number, VariableItem>();
        this.outputs = outputs ?? new Map<number, VariableItem>();
    }

    // TODO: Add variable type to here
    addNode(x: number, y: number, schemaId: string, inputValues: IdValuePair[], inputSlots: ItemSlotPair[], inputFlowSlots: ItemSlotPair[], outputSlots: Map<number, ItemSlotPair[]>, outputFlowSlots: ItemSlotPair[], customName?: string, variableId?: number, variableType?: string) {
        this.addNodes(false, {x, y, schemaId, inputValues, inputSlots, inputFlowSlots, outputSlots, outputFlowSlots, customName, variableId, variableType});
    }

    // TODO: reloadOnFinish is only needed when seeding the db, seems hackish...
    addNodes(reloadOnFinish: boolean, ...nodes: {x: number, y: number, schemaId: string, inputValues: IdValuePair[], inputSlots: ItemSlotPair[], inputFlowSlots: ItemSlotPair[], outputSlots: Map<number, ItemSlotPair[]>, outputFlowSlots: ItemSlotPair[], customName?: string, variableId?: number, variableType?: string}[]) {
        nodes.forEach(node => {
            const key = this.itemCounter++;
            this.items.set(key, {id: key, x: node.x, y: node.y, schemaId: node.schemaId, inputValues: node.inputValues, inputSlots: node.inputSlots, inputFlowSlots: node.inputFlowSlots, outputSlots: node.outputSlots, outputFlowSlots: node.outputFlowSlots, customName: node.customName, variableId: node.variableId, variableType: node.variableType});
        });

        VisualScriptingDB.updateScriplet(this.id, {"items": this.items, "itemCounter": this.itemCounter}).then(result => {
            if (result && reloadOnFinish)
                window.location.reload();
        });
    }

    getStart(): NodeItem {
        if (!(this.start as NodeItem).schema) {
            const newNode = new NodeItem(this.start.id, this.start.x, this.start.y, this.start.schemaId, this.start.inputValues, this.start.inputSlots, this.start.inputFlowSlots, this.start.outputSlots, this.start.outputFlowSlots, this.start.customName, this.start.variableId, this.start.variableType);
            this.start = newNode;
            return newNode;
        }
        return this.start as NodeItem;
    }

    getNode(itemId: number): NodeItem | undefined {
        const result = this.items.get(itemId);
        if (!result)
            return;
        
        return this.convertToNodeItem(result);
    }

    getNodes(): NodeItem[] {
        const result: NodeItem[] = [];
        this.items.forEach(node => {
            result.push(this.convertToNodeItem(node));
        });

        return result;
    }

    convertToNodeItem(node: INodeItem): NodeItem {
        if (!(node as NodeItem).schema) {
            const newNode = new NodeItem(node.id, node.x, node.y, node.schemaId, node.inputValues, node.inputSlots, node.inputFlowSlots, node.outputSlots, node.outputFlowSlots, node.customName, node.variableId, node.variableType);
            this.items.set(node.id, newNode);
            return newNode;
        }

        return node as NodeItem;
    }

    updateNodes() {
        VisualScriptingDB.updateScriplet(this.id, {"items": this.items});
    }

    deleteNode(itemId: number): boolean {
        return this.deleteNodes(itemId);
    }

    deleteNodes(...itemIds: number[]): boolean {
        let result = true;
        itemIds.forEach(itemId => {
            if(!this.items.delete(itemId))
                result = false;
        });

        VisualScriptingDB.updateScriplet(this.id, {"items": this.items});
        return result;
    }

    addVariable(name: string, type: string, startValue: any, value: any) {
        this.addVariables({name, type, startValue, value});
    }

    addVariables(...variables: {name: string, type: string, startValue: any, value: any}[]) {
        variables.forEach(variable => {
            const key = this.variableCounter++;
            this.variables.set(key, {id: key, name: variable.name, type: variable.type, startValue: variable.startValue, value: variable.value});
        });

        VisualScriptingDB.updateScriplet(this.id, {"variables": this.variables, "variableCounter": this.variableCounter});
    }

    getVariable(id: number): VariableItem | undefined {
        return this.variables.get(id);
    }

    getVariables() {
        return [...this.variables.values()];
    }

    updateVariables() {
        VisualScriptingDB.updateScriplet(this.id, {"variables": this.variables});
    }

    // TODO: Be able to set a default value for the variable, which should also be serialized to db
    setVariable(variable: VariableItem) {
        this.variables.set(variable.id, variable);
    }

    resetVariables() {
        this.variables.forEach(variable => {
            variable.value = undefined;
        });
    }

    deleteVariable(id: number): boolean {
        if (this.variables.delete(id)) {
            this.updateVariables();
            return true;
        }

        return false;
    }

    addInput(name: string, type: string, startValue: any, value: any) {
        this.addInputs({name, type, startValue, value});
    }

    addInputs(...inputs: {name: string, type: string, startValue: any, value: any}[]) {
        inputs.forEach(input => {
            const key = this.inputCounter++;
            this.inputs.set(key, {id: key, name: input.name, type: input.type, startValue: input.startValue, value: input.value});
        });

        VisualScriptingDB.updateScriplet(this.id, {"inputs": this.inputs, "inputCounter": this.inputCounter});
    }

    getInput(id: number): VariableItem | undefined {
        return this.inputs.get(id);
    }

    getInputs() {
        return [...this.inputs.values()];
    }

    updateInputs() {
        VisualScriptingDB.updateScriplet(this.id, {"inputs": this.inputs});
    }

    // TODO: Be able to set a default value for the variable, which should also be serialized to db
    setInput(input: VariableItem) {
        this.inputs.set(input.id, input);
        this.updateInputs();
    }

    deleteInput(id: number): boolean {
        if (this.inputs.delete(id)) {
            this.updateInputs();
            return true;
        }

        return false;
    }

    addOutput(name: string, type: string, startValue: any, value: any) {
        this.addOutputs({name, type, startValue, value});
    }

    addOutputs(...outputs: {name: string, type: string, startValue: any, value: any}[]) {
        outputs.forEach(output => {
            const key = this.outputCounter++;
            this.outputs.set(key, {id: key, name: output.name, type: output.type, startValue: output.startValue, value: output.value});
        });

        VisualScriptingDB.updateScriplet(this.id, {"outputs": this.outputs, "outputCounter": this.outputCounter});
    }

    getOutput(id: number): VariableItem | undefined {
        return this.outputs.get(id);
    }

    getOutputs() {
        return [...this.outputs.values()];
    }

    updateOutputs() {
        VisualScriptingDB.updateScriplet(this.id, {"outputs": this.outputs});
    }

    // TODO: Be able to set a default value for the variable, which should also be serialized to db
    setOutput(output: VariableItem) {
        this.outputs.set(output.id, output);
        this.updateOutputs();
    }

    deleteOutput(id: number): boolean {
        if (this.outputs.delete(id)) {
            this.updateOutputs();
            return true;
        }

        return false;
    }
}