import React from 'react';
import { VariableItem } from '../models/Scriplet';

export type VariableFormProps = {
    variable?: VariableItem;
    onSaveVariable: (name: string, type: string, startValue: any) => void;
    onCancel: () => void;
}

export type VariableFormState = {
    selectedId: number;
    name: string;
    selectedType: string;
    startValue: any;
}

export default class VariableForm extends React.Component<VariableFormProps, VariableFormState> {
    constructor(props: VariableFormProps) {
        super(props);

        this.state = {
            selectedId: -1,
            name: "",
            selectedType: "String",
            startValue: undefined
        }

        this.setStartValue = this.setStartValue.bind(this);
    }

    componentDidUpdate() {
        const { variable } = this.props;
        const { selectedId } = this.state;
        if (!variable)
            return;

        if (selectedId !== variable.id)
            this.setState({selectedId: variable.id, name: variable.name, selectedType: variable.type, startValue: variable.startValue});
    }
    
    setStartValue(inputValue: string) {
        let startValue;

        switch (this.state.selectedType) {
            case "Number":
                startValue = parseInt(inputValue);
                break;
            case "Boolean":
                startValue = (inputValue === "true");
                break;
            default:
                startValue = inputValue;
                break;
        }

        this.setState({startValue: startValue});
    }

    render() {
        const { name, selectedType, startValue } = this.state;
        const { onSaveVariable, onCancel } = this.props;

        return(
            <div className="flex flex-col">
                <div className="flex flex-col mb-4">
                    <label className="mb-1 text-sm text-gray-700">
                        Name
                    </label>
                    <input className="px-2 border-2 rounded" 
                            type="text" 
                            name="title" 
                            value={name} 
                            onChange={event => {
                                this.setState({name: event.target.value});
                            }} />
                </div>
                <div className="flex flex-col mb-4">
                    <label className="mb-1 text-sm text-gray-700">
                        Type
                    </label>
                    <select className="px-1 border-2 rounded text-gray-800" 
                            name="" 
                            id="" 
                            value={selectedType}
                            onChange={event => {
                                this.setState({selectedType: event.target.value});
                            }}>
                        {/* TODO: Base this off of the Variable Type */}
                        <option value="Number">Number</option>
                        <option value="Boolean">Boolean</option>
                        <option value="String">String</option>
                    </select>
                </div>
                <div className="flex flex-col mb-6">
                    <label className="mb-1 text-sm text-gray-700">
                        Start Value
                    </label>
                    {(selectedType === "Number" && 
                        <input className="px-2 border-2 rounded" 
                                type="number" 
                                name="title" 
                                value={`${startValue ?? ""}`}
                                onChange={event => this.setStartValue(event.target.value)} />
                    ) || 
                    (selectedType === "Boolean" && 
                        <select className="px-2 border-2 rounded text-gray-800" 
                                name="" 
                                id=""
                                value={`${startValue ?? ""}`}
                                onChange={event => this.setStartValue(event.target.value)}>
                            <option value="" disabled hidden>Select a value...</option>
                            <option value="true">True</option>
                            <option value="false">False</option>
                        </select>
                    ) ||
                    ((selectedType === "String" || selectedType === "") &&
                        <input className="px-2 border-2 rounded" 
                                type="text" 
                                name="title" 
                                value={`${startValue ?? ""}`}
                                onChange={event => this.setStartValue(event.target.value)} />
                    )}
                </div>
                <div className="flex flex-row-reverse">
                    <button className="ml-4 px-2 py-1 rounded bg-blue-600 text-gray-100 hover:bg-blue-500" 
                            onClick={() => {
                                // TODO: Do validation feedback eventually
                                if (!name || !selectedType || startValue == null || startValue === "") {
                                    console.error(`The form for creating a variable cannot have empty fields.`);
                                    return;
                                }

                                onSaveVariable(name, selectedType, startValue);
                                onCancel();
                            }}>
                        Save
                    </button>
                    <button className="px-2 py-1 rounded bg-gray-500 text-gray-100 hover:bg-gray-400" 
                            onClick={onCancel}>
                        Cancel
                    </button>
                </div>
            </div>
        );
    }
}