import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';

export type SearchBarProps = {
    filterSearch: (searchValue: string) => void;
}

export type SearchBarState = {
    searchValue: string;
}

export default class SearchBar extends React.Component<SearchBarProps, SearchBarState> {
    constructor(props: SearchBarProps) {
        super(props);

        this.state = {
            searchValue: ""
        }

        this.onSearchChange = this.onSearchChange.bind(this);
        this.onSearchReset = this.onSearchReset.bind(this);
    }

    componentDidMount() {
        this.props.filterSearch("");
    }

    onSearchChange(event: React.ChangeEvent<HTMLInputElement>) {
        const searchValue = event.target.value;
        this.setState({searchValue});
        this.props.filterSearch(searchValue);
    }

    onSearchReset() {
        this.setState({searchValue: ""});
        this.props.filterSearch("");
    }

    render() {
        return (
            <div className="h-6 flex flex-row w-full">
                <div className="px-2 rounded-l-full bg-gray-100">
                    <FontAwesomeIcon icon="search" 
                                        className="text-sm text-gray-700" />
                </div>
                
                <input type="text" 
                            value={this.state.searchValue}
                            className="w-full bg-gray-100 border-none text-sm"
                            onChange={this.onSearchChange} />
                <button className="px-2 flex items-center rounded-r-full bg-gray-100"
                        onClick={this.onSearchReset}>
                    <FontAwesomeIcon icon="times-circle" 
                                        className="text-sm text-gray-700" />
                </button>
            </div>
        );
    }
}