import React from 'react';
import { VariableItem } from '../models/Scriplet';
import DropdownButton from './DropdownButton';

export type VariableNavItemProps = {
    variable: VariableItem;
    onDragStart: (event: React.DragEvent<HTMLDivElement>) => void;
    onDragEnd: (event: React.DragEvent<HTMLDivElement>) => void;
    onEdit: (variableId: number) => void;
    onDelete: (variableId: number) => void;
}

export type VariableNavItemState = {
    
}

export default class VariableNavItem extends React.Component<VariableNavItemProps, VariableNavItemState> {
    constructor(props: VariableNavItemProps) {
        super(props);

        this.state = {
            
        }
    }

    render() {
        const { variable, onDragStart, onDragEnd, onEdit, onDelete } = this.props;

        return(
            <div className="mb-2 select-none" 
                    draggable
                    onDragStart={onDragStart}
                    onDragEnd={onDragEnd}>
                {/* hover:bg-gray-700 */}
                <div className="flex flex-row items-center px-2 py-1 rounded hover:bg-gray-700" style={{cursor: "grab"}}>
                    <div className="flex flex-col w-full mr-2">
                        <div className="text-sm text-gray-100">
                            {variable.name}
                        </div>
                        <div className="text-xs text-gray-300">
                            {variable.type}
                        </div>
                    </div>
                    <div className="px-2 py-2 w-full text-gray-100">
                        <div className="px-1 rounded-sm bg-gray-600">
                            {`${variable.startValue}`}
                        </div>
                    </div>
                    <DropdownButton onEdit={() => onEdit(variable.id)}
                                    onDelete={() => onDelete(variable.id)} />
                </div>
            </div>
        );
    }
}