import { BaseNodeSchema } from "./NodeSchema";
import NodeSchemaManager from "../managers/NodeSchemaManager";

export interface ICategorizable {
    id: any;
    name: string;
}

export abstract class Category<T extends ICategorizable> {
    public name: string;
    public subCategories: Category<T>[];
    public contentChildren: T[];

    constructor(name: string) {
        this.name = name;
        this.subCategories = [];
        this.contentChildren = [];
    }

    abstract getSubCategory(name: string): Category<T>;
    abstract addContent(id: any): void;
}

export class SchemaCategory extends Category<BaseNodeSchema> {
    getSubCategory(name: string): SchemaCategory {
        let subCategory = this.subCategories.find(cat => cat.name === name);
        if (!subCategory) {
            subCategory = new SchemaCategory(name);
            this.subCategories.push(subCategory);
        }

        return subCategory;
    }

    addContent(id: string) {
        if (this.contentChildren.find(schema => schema.name === id)) {
            console.error(`A duplicate of the schema '${id}' was found.`);
            return;
        }

        const schema = NodeSchemaManager.getBaseSchema(id);
        this.contentChildren.push(schema);
    }
}