import Dexie from 'dexie';
import { INodeItem } from '../models/NodeItem';
import { BaseSchemaTypes } from '../models/NodeSchema';
import { ItemSlotPair } from '../models/NodeSlot';
import { IScriplet, Scriplet, VariableItem } from '../models/Scriplet';

class VisualScriptingDB extends Dexie {
    public scriplets: Dexie.Table<IScriplet, number>;

    constructor() {
        super("VisualScriptingDB");

        this.version(1).stores({
            scriplets: "++id, name"
        });
        this.open().catch(error => {
            console.error(`Opening the db '${this.name}' failed: ${error}`);
        });

        this.scriplets = this.table("scriplets");
        this.scriplets.mapToClass(Scriplet);

        this.seedData();
    }

    seedData() {
        this.scriplets.toArray().then(scriplets => {
            if (scriplets.length <= 0) {
                const start: INodeItem = {id: 0, x: 0, y: 0, schemaId: BaseSchemaTypes.Start, inputValues: [], inputSlots: [], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: [{id: 0, itemId: 4, slotId: 0}], customName: undefined, variableId: undefined};
                this.addScriplet("Check If Prime", "Checks if the provided number is a prime.", start).then(scriplet => {
                    if (!scriplet) {
                        console.error("Something went wrong when creating a scriplet. Check if scriplet name is not empty.");
                        return;
                    }

                    /*
                    scriplet.addNodes(true,
                        {x: -530, y: 240, schemaId: BaseSchemaTypes.InputNumber, inputValues: [{id: 0, value: 97}], inputSlots: [], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 2, slotId: 0}, {id: 0, itemId: 3, slotId: 0}]]]), outputFlowSlots: [], customName: "Number To Check"},
                        {x: -290, y: 118, schemaId: BaseSchemaTypes.DivideNumbers, inputValues: [{id: 1, value: 2}], inputSlots: [{id: 0, itemId: 1, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 3, slotId: 0}]]]), outputFlowSlots: [], customName: "Half Number"},
                        {x: -32, y: 118, schemaId: BaseSchemaTypes.AddNumbers, inputValues: [{id: 1, value: 1}], inputSlots: [{id: 0, itemId: 2, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 4, slotId: 3}]]]), outputFlowSlots: [], customName: "Plus One"},
                        {x: 225, y: 0, schemaId: BaseSchemaTypes.ForLoopBreak, inputValues: [{id: 2, value: 2}], inputSlots: [{id: 3, itemId: 3, slotId: 0}], inputFlowSlots: [{id: 0, itemId: 0, slotId: 0}, {id: 1, itemId: 8, slotId: 0}], outputSlots: new Map<number, ItemSlotPair[]>([[2, [{id: 2, itemId: 5, slotId: 1}]]]), outputFlowSlots: [{id: 0, itemId: 7, slotId: 0}, {id: 1, itemId: 9, slotId: 0}]},
                        {x: 473, y: 240, schemaId: BaseSchemaTypes.ModuloNumbers, inputValues: [], inputSlots: [{id: 0, itemId: 1, slotId: 0}, {id: 1, itemId: 4, slotId: 2}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 6, slotId: 0}]]]), outputFlowSlots: []},
                        {x: 743, y: 132, schemaId: BaseSchemaTypes.EqualNumbers, inputValues: [], inputSlots: [{id: 0, itemId: 5, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 7, slotId: 1}]]]), outputFlowSlots: [], customName: "Equals Zero"},
                        {x: 986, y: -18, schemaId: BaseSchemaTypes.IfElse, inputValues: [], inputSlots: [{id: 1, itemId: 6, slotId: 0}], inputFlowSlots: [{id: 0, itemId: 4, slotId: 0}], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: [{id: 0, itemId: 8, slotId: 0}], customName: "Check If Prime"},
                        {x: 1250, y: 100, schemaId: BaseSchemaTypes.PrintBoolean, inputValues: [{id: 1, value: false}], inputSlots: [], inputFlowSlots: [{id: 0, itemId: 7, slotId: 0}], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: [{id: 0, itemId: 4, slotId: 1}], customName: "Is Not Prime"},
                        {x: 864, y: 278, schemaId: BaseSchemaTypes.PrintBoolean, inputValues: [], inputSlots: [], inputFlowSlots: [{id: 0, itemId: 4, slotId: 1}], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: [], customName: "Is Prime"}
                    );
                    */

                    scriplet.addVariables({name: "Is Prime", type: "Boolean", startValue: true, value: undefined});
                    scriplet.addInputs({name: "Number to Check", type: "Number", startValue: 97, value: undefined});
                   
                    scriplet.addNodes(true,
                        {x: -530, y: 240, schemaId: BaseSchemaTypes.InputNumber, inputValues: [], inputSlots: [], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 2, slotId: 0}, {id: 0, itemId: 3, slotId: 0}]]]), outputFlowSlots: [], customName: "Number To Check", variableId: 1, variableType: "Input"},
                        {x: -290, y: 118, schemaId: BaseSchemaTypes.DivideNumbers, inputValues: [{id: 1, value: 2}], inputSlots: [{id: 0, itemId: 1, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 3, slotId: 0}]]]), outputFlowSlots: [], customName: "Half Number"},
                        {x: -32, y: 118, schemaId: BaseSchemaTypes.AddNumbers, inputValues: [{id: 1, value: 1}], inputSlots: [{id: 0, itemId: 2, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 4, slotId: 3}]]]), outputFlowSlots: [], customName: "Plus One"},
                        {x: 225, y: 0, schemaId: BaseSchemaTypes.ForLoopBreak, inputValues: [{id: 2, value: 2}], inputSlots: [{id: 3, itemId: 3, slotId: 0}], inputFlowSlots: [{id: 0, itemId: 0, slotId: 0}, {id: 1, itemId: 8, slotId: 0}], outputSlots: new Map<number, ItemSlotPair[]>([[2, [{id: 2, itemId: 5, slotId: 1}]]]), outputFlowSlots: [{id: 0, itemId: 7, slotId: 0}, {id: 1, itemId: 9, slotId: 0}]},
                        {x: 473, y: 240, schemaId: BaseSchemaTypes.ModuloNumbers, inputValues: [], inputSlots: [{id: 0, itemId: 1, slotId: 0}, {id: 1, itemId: 4, slotId: 2}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 6, slotId: 0}]]]), outputFlowSlots: []},
                        {x: 743, y: 132, schemaId: BaseSchemaTypes.EqualNumbers, inputValues: [], inputSlots: [{id: 0, itemId: 5, slotId: 0}], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 7, slotId: 1}]]]), outputFlowSlots: [], customName: "Equals Zero"},
                        {x: 986, y: -18, schemaId: BaseSchemaTypes.IfElse, inputValues: [], inputSlots: [{id: 1, itemId: 6, slotId: 0}], inputFlowSlots: [{id: 0, itemId: 4, slotId: 0}], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: [{id: 0, itemId: 8, slotId: 0}], customName: "Check If Prime"},
                        {x: 1250, y: 100, schemaId: BaseSchemaTypes.SetBoolean, inputValues: [{id: 1, value: false}], inputSlots: [], inputFlowSlots: [{id: 0, itemId: 7, slotId: 0}], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: [{id: 0, itemId: 4, slotId: 1}], customName: "Set Is Prime", variableId: 1, variableType: "Variable"},
                        {x: 864, y: 278, schemaId: BaseSchemaTypes.IfElse, inputValues: [], inputSlots: [{id: 1, itemId: 12, slotId: 0}], inputFlowSlots: [{id: 0, itemId: 4, slotId: 1}], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: [{id: 0, itemId: 10, slotId: 0}, {id: 1, itemId: 11, slotId: 0}], customName: "If IsPrime is True"},
                        {x: 1164, y: 278, schemaId: BaseSchemaTypes.PrintBoolean, inputValues: [], inputSlots: [], inputFlowSlots: [{id: 0, itemId: 9, slotId: 0}], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: [], customName: "Is Definitely Prime"},
                        {x: 1164, y: 420, schemaId: BaseSchemaTypes.PrintBoolean, inputValues: [{id: 1, value: false}], inputSlots: [], inputFlowSlots: [{id: 0, itemId: 9, slotId: 1}], outputSlots: new Map<number, ItemSlotPair[]>(), outputFlowSlots: [], customName: "Is Definitely Not Prime"},
                        {x: 564, y: 400, schemaId: BaseSchemaTypes.GetBoolean, inputValues: [], inputSlots: [], inputFlowSlots: [], outputSlots: new Map<number, ItemSlotPair[]>([[0, [{id: 0, itemId: 9, slotId: 1}]]]), outputFlowSlots: [], customName: "Get Is Prime", variableId: 1, variableType: "Variable"}
                    );
                });

                this.addScriplet("Empty Scriplet").then(scriplet => {
                    if (!scriplet) {
                        console.error("Something went wrong when creating a scriplet. Check if scriplet name is not empty.");
                        return;
                    }

                    scriplet.addVariables({name: "Test Variable", type: "Number", startValue: 16, value: undefined});

                    scriplet.addNodes(true, 
                        {x: 0, y: 100, schemaId: BaseSchemaTypes.GetNumber, inputValues: [], inputSlots: [], inputFlowSlots: [], outputSlots: new Map(), outputFlowSlots: [], customName: "Test Name", variableId: 1},
                        {x: 0, y: 200, schemaId: BaseSchemaTypes.SetNumber, inputValues: [], inputSlots: [], inputFlowSlots: [], outputSlots: new Map(), outputFlowSlots: [], customName: "Test Name 2", variableId: 1}
                    );
                });
            }
        });
    }

    async getScriplet(id: number): Promise<Scriplet | undefined> {
        const scriplet = await this.scriplets.get(id);
        if (!scriplet)
            return;
        return new Scriplet(scriplet.name, scriplet.id, scriplet.description, scriplet.start, scriplet.items, scriplet.itemCounter, scriplet.variables, scriplet.variableCounter, scriplet.inputs, scriplet.inputCounter, scriplet.outputs, scriplet.outputCounter);
    }

    async getScriplets(): Promise<Scriplet[]> {
        const scriplets = await this.scriplets.toArray();
        const result: Scriplet[] = [];
        scriplets.forEach(scriplet => {
            result.push(new Scriplet(scriplet.name, scriplet.id, scriplet.description, scriplet.start, scriplet.items, scriplet.itemCounter, scriplet.variables, scriplet.variableCounter, scriplet.inputs, scriplet.inputCounter, scriplet.outputs, scriplet.outputCounter));
        });
        return result;
    }

    async addScriplet(name: string, description?: string, start?: INodeItem, items?: Map<number, INodeItem>, itemCounter?: number, variables?: Map<number, VariableItem>, variableCounter?: number, inputs?: Map<number, VariableItem>, inputCounter?: number, outputs?: Map<number, VariableItem>, outputCounter?: number): Promise<Scriplet | undefined> {
        if (!name) {
            console.error("When creating a scriplet, it cannot have an empty name.");
            return;
        }

        const scriplet: IScriplet = {name, description, start, items, itemCounter, variables, variableCounter, inputs, inputCounter, outputs, outputCounter}; 
        const id = await this.scriplets.add(scriplet);
        return new Scriplet(name, id, description, start, items, itemCounter, variables, variableCounter, inputs, inputCounter, outputs, outputCounter);
    }

    async updateScriplet(id: number, changes: {[keyPath: string]: any}): Promise<boolean> {
        const result = await this.scriplets.update(id, changes);
        if (result === 0) {
            console.error(`The scriplet of id '${id}' could not be found in the database; nothing was updated.`);
            return false;
        }

        return true;
    }
    
    async updateEntireScriplet(scriplet: Scriplet): Promise<boolean> {
        const result = await this.scriplets
                                .where(":id")
                                .equals(scriplet.id)
                                .modify((value, ref) => ref.value = scriplet);
        
        if (result === 0) {
            console.error(`The scriplet of id '${scriplet.id}' could not be found in the database; nothing was updated.`);
            return false;
        }

        return true;
    }

    async deleteScriplet(id: number) {
        await this.scriplets.delete(id);
    }

    async resetDatabase() {
        return await this.delete();
    }
}

export default new VisualScriptingDB();