import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'react-router-dom';
import Scrollbar from 'react-scrollbars-custom';
import VisualScriptingDB from '../managers/VisualScriptingDB';
import { Scriplet } from '../models/Scriplet';
import Modal from './Modal';
import ScripletNavItem from './ScripletNavItem';
import AddItem from './AddItem';
import ScripletForm from './ScripletForm';
import SettingsForm from './SettingsForm';

import logo from '../assets/images/logo192.png';

export type SidebarProps = {
    
}

export type SidebarState = {
    scriplets: Scriplet[];
    editedScriplet: Scriplet | undefined;
    isDirtyDB: boolean;
    isNavMenuOpen: boolean;
    isSidebarOpen: boolean;
    openAddScripletModal: () => void;
    openEditScripletModal: () => void;
    openSettingsModal: () => void;
}

export default class SidebarNav extends React.Component<SidebarProps, SidebarState> {
    private scripletNameInput: React.RefObject<HTMLInputElement>;

    constructor(props: SidebarProps) {
        super(props);

        this.scripletNameInput = React.createRef<HTMLInputElement>();

        this.state = {
            scriplets: [],
            editedScriplet: undefined,
            isDirtyDB: false,
            isNavMenuOpen: false,
            isSidebarOpen: true,
            openAddScripletModal: () => {},
            openEditScripletModal: () => {},
            openSettingsModal: () => {}
        };

        this.openNavMenu = this.openNavMenu.bind(this);
        this.closeNavMenu = this.closeNavMenu.bind(this);
        this.toggleNavMenu = this.toggleNavMenu.bind(this);
        this.openSidebar = this.openSidebar.bind(this);
        this.closeSidebar = this.closeSidebar.bind(this);
        this.toggleSidebar = this.toggleSidebar.bind(this);
        this.onScripletCreate = this.onScripletCreate.bind(this);
        this.onScripletDelete = this.onScripletDelete.bind(this);
    }

    componentDidMount() {
        VisualScriptingDB.getScriplets().then(scriplets => {
            this.setState({scriplets: scriplets});
        });
    }

    componentDidUpdate() {
        if (this.state.isDirtyDB) {
            VisualScriptingDB.getScriplets().then(scriplets => {
                this.setState({scriplets: scriplets, isDirtyDB: false});
            });
        }

        const input = this.scripletNameInput.current;
        if (!input)
            return;

        if (input.classList.contains("hidden"))
            return;

        input.focus();
    }

    openNavMenu() {
        this.setState({isNavMenuOpen: true});
    }

    closeNavMenu() {
        this.setState({isNavMenuOpen: false});
    }

    toggleNavMenu() {
        if (this.state.isNavMenuOpen)
            this.closeNavMenu();
        else
            this.openNavMenu();
    }

    openSidebar() {
        this.setState({isSidebarOpen: true});
    }

    closeSidebar() {
        this.setState({isSidebarOpen: false});
    }

    toggleSidebar() {
        if (this.state.isSidebarOpen)
            this.closeSidebar();
        else
            this.openSidebar();
    }

    onScripletCreate(name: string, description: string) {
        VisualScriptingDB.addScriplet(name, description).then(scriplet => {
            if (scriplet)
                this.setState({isDirtyDB: true});
        });
    }

    onScripletEdit(id: number, name: string, description: string) {
        VisualScriptingDB.updateScriplet(id, {"name": name, "description": description}).then(result => {
            if (result)
                this.setState({isDirtyDB: true});
        });
    }

    onScripletDelete(scripletId: number) {
        VisualScriptingDB.deleteScriplet(scripletId).then(() => {
            window.location.replace("/vso");
        });
    }

    render() {
        const { scriplets, editedScriplet, isNavMenuOpen, isSidebarOpen, openAddScripletModal, openEditScripletModal, openSettingsModal } = this.state;
        return (
            <nav className="nav">
                {/* 
                * Plan is to have a top navigation for md and below
                * screen sizes, but have sidebar for lg and above
                * 
                * Ignore animations for now
                */}

                { /* Top navbar */ }
                <div className="topbar">
                    <div className="lg:hidden flex items-center justify-between p-4 bg-gray-800">
                        { /* Logo */ }
                        <div className="">
                            <Link to="/" className="flex flex-row text-xl text-gray-100 font-semibold">
                                <img className="mr-4" src={logo} alt="Logo" width={32} />
                                Visual Scripting
                            </Link>
                        </div>

                        <div className="flex flex-row">
                            {/* Settings */}
                            <div className="">
                                <button className="px-3 py-2 mr-3 flex items-center text-indigo-500 rounded border border-indigo-500 hover:bg-indigo-300 cursor-pointer select-none"
                                        onClick={() => openSettingsModal()}>
                                    <FontAwesomeIcon icon="cog" />
                                </button>
                            </div>

                            { /* Hamburger for sidebar items */ }
                            <div className="block">
                                <button className="flex items-center py-2 px-3 text-indigo-500 rounded border border-indigo-500 hover:bg-indigo-300"
                                        onClick={this.toggleNavMenu} >
                                    <FontAwesomeIcon icon="bars" />
                                </button>
                            </div>
                        </div>
                    </div>

                    { /* Nav Items */ }
                    <div className={`${isNavMenuOpen ? '' : 'hidden'} lg:hidden relative z-20`}>
                        <div className="absolute w-full px-4 pb-4 bg-gray-800">
                            {
                                scriplets.map((scriplet, i) => (
                                    <Link key={i} 
                                            onClick={this.closeNavMenu} 
                                            to={"/scriplet/" + scriplet.id} 
                                            className="block mb-1 px-4 py-2 rounded-md hover:bg-gray-900 focus:bg-gray-900 text-gray-100">
                                        <FontAwesomeIcon className="mr-4 text-lg text-gray-600" icon="folder" />
                                        {scriplet.name}
                                    </Link>
                                ))
                            }
                            <div className={`${scriplets.length > 0 ? "hidden" : "flex"} justify-center mb-2 select-none`}>
                                <div className="p-3 text-gray-400 italic">
                                    No scriplets found
                                </div>
                            </div>
                            <div className="w-full mb-1 rounded-md border-gray-600 border-dashed border-2 cursor-pointer hover:bg-gray-900 focus:bg-gray-900 text-gray-500">
                                <div className={`px-4 py-2`}
                                        onClick={() => {
                                            openAddScripletModal();
                                        }}>
                                    <FontAwesomeIcon className="mr-4 text-lg text-gray-600" 
                                                    icon="plus" />
                                    <span className="font-medium">Add Scriplet</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                { /* Sidebar */ }
                <div>
                    <div className={`${isSidebarOpen ? "sidebar shadow" : "hidden"}`}>
                        <div className="flex-grow h-screen bg-gray-800 relative">
                            { /* Logo */ }
                            <div className="p-4 h-16">
                                <Link to="/" className="flex flex-row text-xl text-gray-100 font-semibold">
                                    <img className="mr-4" src={logo} alt="Logo" width={32} />
                                    Visual Scripting
                                </Link>
                            </div>

                            { /* Sidebar Items */ }
                            <div className="p-2 my-auto lg:w-64" style={{height: "calc(100vh - 8rem)"}}>
                                { /* TODO: Place the dashboard item here */ }
                                <Scrollbar noScrollX>
                                    {
                                        scriplets.map((scriplet, i) => (
                                            <ScripletNavItem key={`scriplet-${i}`}
                                                                scriplet={scriplet}
                                                                closeNavMenu={this.closeNavMenu}
                                                                onEdit={() => this.setState({editedScriplet: scriplet}, () => openEditScripletModal())}
                                                                onDelete={this.onScripletDelete} />
                                        ))
                                    }
                                    <div className={`${scriplets.length > 0 ? "hidden" : "flex"} justify-center mb-2 select-none`}>
                                        <div className="p-3 text-gray-400 italic">
                                            No scriplets found
                                        </div>
                                    </div>
                                    <div className="w-full mb-1 rounded-md border-gray-600 border-dashed border-2 cursor-pointer hover:bg-gray-900 focus:bg-gray-900 text-gray-500">
                                        <div className={`px-4 py-2`}
                                                onClick={() => {
                                                    openAddScripletModal();
                                                }}>
                                            <FontAwesomeIcon className="mr-4 text-lg text-gray-600" 
                                                            icon="plus" />
                                            <span className="font-medium">Add Scriplet</span>
                                        </div>
                                    </div>
                                </Scrollbar>
                            </div>

                            {/* Settings */}
                            <div className="px-4 py-3 h-16">
                                <button className="px-4 py-2 w-full flex items-center rounded bg-gray-700 hover:bg-gray-600 text-gray-100 cursor-pointer select-none"
                                        onClick={() => openSettingsModal()}>
                                    <FontAwesomeIcon className="mr-3" icon="cog" />
                                    Settings
                                </button>
                            </div>
                        </div>
                    </div>
                    <div className={`${isSidebarOpen ? "hidden" : "collapsed-sidebar shadow"}`}>
                        <div className="flex-grow h-screen bg-gray-800 relative">
                            { /* Logo */ }
                            <div className="p-4 h-16 text-center">
                                <Link to="/" className="text-xl text-gray-100 font-semibold">
                                    <FontAwesomeIcon className="text-2xl text-indigo-500" 
                                                    icon="project-diagram" />
                                </Link>
                            </div>

                            { /* Sidebar Items */ }
                            <div className="sidebar-items p-2 fixed top-16 bottom-0 my-auto">
                                { /* TODO: Place the dashboard item here */ }
                                <Scrollbar noScrollX>
                                    {
                                        scriplets.map((scriplet, i) => (
                                            <Link key={i} 
                                                    onClick={this.closeNavMenu} 
                                                    to={"/scriplet/" + scriplet.id} 
                                                    className="block border-gray-700 border-solid border hover:border-opacity-0 mb-1 py-2 text-center rounded-md hover:bg-gray-900 focus:bg-gray-900 text-gray-100 font-bold">
                                                {scriplet.name[0]}
                                            </Link>
                                        ))
                                    }
                                    <div className="flex mb-1 py-2 justify-center rounded-md border-gray-600 border-dashed border-2 cursor-pointer hover:bg-gray-900 focus:bg-gray-900 text-gray-500"
                                            onClick={() => {
                                                console.log("test");
                                                openAddScripletModal();
                                            }}>
                                        <div className="inline-block">
                                            <FontAwesomeIcon className="text-lg text-gray-600" 
                                                            icon="plus" />
                                        </div>
                                    </div>
                                </Scrollbar>
                            </div>
                        </div>
                    </div>
                    <button className={`${isSidebarOpen ? "w-64 ml-8 pl-56" : "w-16 ml-8 pl-8"} sidebar-toggle items-center rounded-full bg-gray-700`}
                            onClick={this.toggleSidebar}>
                        <div className="inline-block pl-2">
                            <FontAwesomeIcon icon="chevron-left"
                                                className={`${isSidebarOpen ? "block" : "hidden"} text-gray-300`} />
                            <FontAwesomeIcon icon="chevron-right"
                                                className={`${isSidebarOpen ? "hidden" : "block"} text-gray-300`} />
                        </div>
                    </button>
                </div>

                {/* Modals here */}
                <Modal title="Create New Scriplet"
                        openModal={callable => {
                            if (openAddScripletModal.toString() === (() => {}).toString())
                                this.setState({openAddScripletModal: callable});
                        }}>
                    {(closeModal) => (
                        <ScripletForm onSaveScriplet={(name, description) => {
                                                            this.onScripletCreate(name, description);
                                                        }}
                                        onCancel={closeModal} />
                    )}
                </Modal>
                <Modal title="Edit Scriplet"
                        openModal={callable => {
                            if (openEditScripletModal.toString() === (() => {}).toString())
                                this.setState({openEditScripletModal: callable});
                        }}>
                    {(closeModal) => (
                        <ScripletForm scriplet={editedScriplet}
                                        onSaveScriplet={(name, description) => {
                                                            if (!editedScriplet) {
                                                                console.error(`No edited scriplet could be found.`);
                                                                return;
                                                            }

                                                            this.onScripletEdit(editedScriplet.id, name, description);
                                                        }}
                                        onCancel={closeModal} />
                    )}
                </Modal>
                <Modal title="Settings"
                        openModal={callable => {
                            if (openSettingsModal.toString() === (() => {}).toString())
                                this.setState({openSettingsModal: callable});
                        }}>
                    {(closeModal) => (
                        <SettingsForm />
                    )}
                </Modal>
            </nav>
        );
    }
}