import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';
import Colors from '../utils/Colors';

export type DropdownButtonProps = {
    onEdit: () => void;
    onDelete: () => void;
}

export type DropdownButtonState = {
    isMoreOptions: boolean;
}

export default class DropdownButton extends React.Component<DropdownButtonProps, DropdownButtonState> {
    private moreOptions: React.RefObject<HTMLDivElement>;
    constructor(props: DropdownButtonProps) {
        super(props);

        this.moreOptions = React.createRef<HTMLDivElement>();

        this.state = {
            isMoreOptions: false
        }

        this.openOptions = this.openOptions.bind(this);
        this.closeOptions = this.closeOptions.bind(this);
        this.toggleOptions = this.toggleOptions.bind(this);
    }

    componentDidUpdate() {
        if (this.state.isMoreOptions)
            this.moreOptions.current?.focus();
    }

    openOptions() {
        this.setState({isMoreOptions: true});
    }

    closeOptions() {
        this.setState({isMoreOptions: false});
    }

    toggleOptions() {
        if (this.state.isMoreOptions)
            this.closeOptions();
        else
            this.openOptions();
    }

    render() {
        const { onEdit, onDelete } = this.props;
        const { isMoreOptions } = this.state;

        return(
            <div className="relative">
                <button className="px-2 py-1 flex items-center rounded bg-blue-600 hover:bg-blue-500"
                        onClick={event => {
                            event.preventDefault();
                            this.toggleOptions();
                        }}>
                    <FontAwesomeIcon className="text-sm text-gray-100" icon="ellipsis-v" />
                </button>
                <div ref={this.moreOptions}
                        className={`${isMoreOptions ? "" : "hidden"} absolute outline-none`} 
                        style={{left: "-54px", zIndex: 10}}
                        tabIndex={0}
                        onBlur={() => {
                            this.closeOptions();
                        }}>
                    <div className="" style={{marginLeft: "56px", width: "0", height: "12px", borderLeft: "8px solid transparent", borderRight: "8px solid transparent", borderBottom: `8px solid ${Colors.Gray[900]}`}}></div>
                    <div className="w-20 rounded shadow bg-gray-900" style={{height: "58px"}}>
                        <div className="px-2 py-1 flex flex-row items-center rounded text-gray-100 hover:bg-gray-600 cursor-pointer"
                                onClick={event => {
                                    event.stopPropagation();
                                    this.closeOptions();
                                    onEdit();
                                }}>
                            <FontAwesomeIcon className="text-xs text-gray-100" icon="pencil-alt" />
                            <div className="w-full flex justify-center text-sm">
                                <div className="">Edit</div>
                            </div>
                        </div>
                        <div className="px-2 py-1 flex flex-row items-center rounded text-gray-100 hover:bg-red-500 cursor-pointer"
                                onClick={event => {
                                    event.stopPropagation();
                                    this.closeOptions();
                                    onDelete();
                                }}>
                            <FontAwesomeIcon className="text-xs text-gray-100" icon="trash" />
                            <div className="w-full flex justify-center text-sm">
                                <div className="">Delete</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}