@AddSchema
export class SetStringSchema extends BaseNodeSchema {
    constructor(itemId: number) {
        const id = BaseSchemaTypes.SetString;
        const name = "Set String";
        const uniqueNamePrefix = "setStr";

        const input1 = new InputSlot(1, String.className, String.defaultValue);
        const inputSlots = [input1];
        const outputSlots = [new OutputSlot(1, String.className, () => {
            return input1.getValue(itemId);
        })];

        const outputFlow1 = new OutputFlowSlot(0, "Exit");
        const outputFlowSlots = [outputFlow1];
        const inputFlow1 = new InputFlowSlot(0, (thisSlot, returnToSlot) => {
            const scriplet = ScripletManager.currentScriplet;
            if (!scriplet) {
                console.error(`The current scriplet could not be found.`);
                return;
            }
    
            const item = scriplet.getNode(itemId);
            if (!item) {
                console.error(`The node item of id '${itemId}' could not be found.`);
                return;
            }
    
            const variableId = item.variableId;
            if (variableId == null) {
                console.error(`The variable id in node item '${itemId}' could not be found.`);
                return;
            }
    
            const variable = scriplet.getVariable(variableId);
            if (!variable) {
                console.error(`The variable of id '${variableId}' could not be found.`);
                return;
            }

            if (variable.type !== String.className) {
                console.error(`Cannot set to variable '${variableId}' of type '${variable.type}' when the needed type is '${Number.className}'.`);
                return;
            }
    
            variable.value = input1.getValue(itemId);
            scriplet.setVariable(variable);

            outputFlow1.goNext(itemId, returnToSlot);
        }, true, "Set Variable");
        const inputFlowSlots = [inputFlow1];

        super(id, name, uniqueNamePrefix, 205, inputFlowSlots, outputFlowSlots, inputSlots, outputSlots, true, false);
    }

    createNewSchema(itemId: number): SetStringSchema {
        return new SetStringSchema(itemId);
    }
}

