@AddSchema
export class ContainsStringSchema extends BaseNodeSchema {
    constructor(itemId: number) {
        const id = BaseSchemaTypes.ContainsString;
        const name = "Contains String";
        const uniqueNamePrefix = "containsStr";
        const input1 = new InputSlot(0, String.className, String.defaultValue, "Searched");
        const input2 = new InputSlot(1, String.className, String.defaultValue, "Search For");
        const input3 = new InputSlot(2, Boolean.className, Boolean.defaultValue, "Case Sensitive?");
        const inputSlots = [input1, input2, input3];

        const output1 = new OutputSlot(0, Boolean.className, () => {
            // TODO: For case-sensitivity, see this and its related link: https://stackoverflow.com/a/19154150/5874473
            if (input3.getValue(itemId) === true)
                return (input1.getValue(itemId) as string).includes(input2.getValue(itemId));

            return (input1.getValue(itemId) as string).toLowerCase().includes((input2.getValue(itemId) as string).toLowerCase());
        }, "Has Found");
        const output2 = new OutputSlot(0, Number.className, () => {
            if (input3.getValue(itemId) === true)
                return (input1.getValue(itemId) as string).indexOf(input2.getValue(itemId));

            return (input1.getValue(itemId) as string).toLowerCase().indexOf((input2.getValue(itemId) as string).toLowerCase());
        }, "Index")
        const outputSlots = [output1, output2];

        super(id, name, uniqueNamePrefix, 240, [], [], inputSlots, outputSlots);
    }

    createNewSchema(itemId: number): ContainsStringSchema {
        return new ContainsStringSchema(itemId);
    }
}

