export class ReplaceSubstringSchema extends BaseNodeSchema {
    constructor(itemId: number) {
        const id = BaseSchemaTypes.ReplaceSubstring;
        const name = "Replace Substring";
        const uniqueNamePrefix = "replaceSubstr";
        const input1 = new InputSlot(0, String.className, String.defaultValue);
        const input2 = new InputSlot(1, String.className, String.defaultValue, "Search For");
        const input3 = new InputSlot(2, String.className, String.defaultValue, "Replace To");
        const inputSlots = [input1, input2, input3];

        const outputSlots = [new OutputSlot(0, String.className, () => {
            return (input1.getValue(itemId) as string).replaceAll(input2.getValue(itemId), input3.getValue(itemId));
        }, "Result")];

        super(id, name, uniqueNamePrefix, 200, [], [], inputSlots, outputSlots);
    }

    createNewSchema(itemId: number): ReplaceSubstringSchema {
        return new ReplaceSubstringSchema(itemId);
    }
}

@AddSchema
export class ReplaceAtIndexStringSchema extends BaseNodeSchema {
    constructor(itemId: number) {
        const id = BaseSchemaTypes.ReplaceAtIndexString;
        const name = "Replace At Index String";
        const uniqueNamePrefix = "replaceAtIndexStr";
        const input1 = new InputSlot(0, String.className, String.defaultValue);
        const input2 = new InputSlot(1, Number.className, Number.defaultValue, "Index From");
        const input3 = new InputSlot(2, Number.className, Number.defaultValue, "Index To");
        const input4 = new InputSlot(3, String.className, String.defaultValue, "Replace To");
        const inputSlots = [input1, input2, input3, input4];

        const outputSlots = [new OutputSlot(0, String.className, () => {
            const input = input1.getValue(itemId) as string;
            return input.substr(0, input2.getValue(itemId)) + input4.getValue(itemId) + input.substr(input3.getValue(itemId));
        }, "Result")];

        super(id, name, uniqueNamePrefix, 200, [], [], inputSlots, outputSlots);
    }

    createNewSchema(itemId: number): ReplaceAtIndexStringSchema {
        return new ReplaceAtIndexStringSchema(itemId);
    }
}

