import React from 'react';
import { Scriplet } from '../models/Scriplet';

export type ScripletFormProps = {
    scriplet?: Scriplet;
    onSaveScriplet: (name: string, description: string) => void;
    onCancel: () => void;
}

export type ScripletFormState = {
    selectedId: number;
    name: string;
    description: string;
}

export default class ScripletForm extends React.Component<ScripletFormProps, ScripletFormState> {
    constructor(props: ScripletFormProps) {
        super(props);

        this.state = {
            selectedId: -1,
            name: "",
            description: ""
        }
    }

    componentDidUpdate() {
        const { scriplet } = this.props;
        const { selectedId } = this.state;
        if (!scriplet)
            return;

        if (selectedId !== scriplet.id)
            this.setState({selectedId: scriplet.id, name: scriplet.name, description: scriplet.description});
    }
    
    render() {
        const { name, description } = this.state;
        const { onSaveScriplet, onCancel } = this.props;

        return(
            <div className="flex flex-col">
                <div className="flex flex-col mb-4">
                    <label className="mb-1 text-sm text-gray-700">
                        Name
                    </label>
                    <input className="px-2 border-2 rounded" 
                            type="text" 
                            name="name" 
                            value={name} 
                            onChange={event => {
                                this.setState({name: event.target.value});
                            }} />
                </div>
                <div className="flex flex-col mb-4">
                    <label className="mb-1 text-sm text-gray-700">
                        Description
                    </label>
                    <textarea className="px-2 border-2 rounded" 
                            name="description" 
                            rows={3}
                            value={description} 
                            onChange={event => {
                                this.setState({description: event.target.value});
                            }} />
                </div>
                <div className="flex flex-row-reverse">
                    <button className="ml-4 px-2 py-1 rounded bg-blue-600 text-gray-100 hover:bg-blue-500" 
                            onClick={() => {
                                // TODO: Do validation feedback eventually
                                if (!name || !description) {
                                    console.error(`The form for creating a scriplet cannot have empty fields.`);
                                    return;
                                }

                                onSaveScriplet(name, description);
                                onCancel();
                            }}>
                        Save
                    </button>
                    <button className="px-2 py-1 rounded bg-gray-500 text-gray-100 hover:bg-gray-400" 
                            onClick={onCancel}>
                        Cancel
                    </button>
                </div>
            </div>
        );
    }
}