import Colors from "../utils/Colors";
import { SimpleEventDispatcher, ISimpleEventHandler } from "ste-simple-events";

export type ConsoleMessage = {
    message: string;
    color: string;
}

class Console {
    private onMessage = new SimpleEventDispatcher<ConsoleMessage>();

    subscribe(func: ISimpleEventHandler<ConsoleMessage>) {
        this.onMessage.subscribe(func)
    }

    log(message: string) {
        this.onMessage.dispatch({message: message, color: Colors.Gray[100]});
    }

    error(message: string) {
        this.onMessage.dispatch({message: message, color: Colors.Red[500]});
    }

    success(message: string) {
        this.onMessage.dispatch({message: message, color: Colors.Green[400]});
    }
}

export default new Console();