import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React from 'react';
import { Link } from 'react-router-dom';
import { Scriplet } from '../models/Scriplet';
import DropdownButton from './DropdownButton';

export type ScripletNavItemProps = {
    scriplet: Scriplet;
    closeNavMenu: () => void;
    onEdit: (scripletId: number) => void;
    onDelete: (scripletId: number) => void;
}

export type ScripletNavItemState = {
    isHovered: boolean;
}

export default class ScripletNavItem extends React.Component<ScripletNavItemProps, ScripletNavItemState> {
    constructor(props: ScripletNavItemProps) {
        super(props);

        this.state = {
            isHovered: false,
        }
    }

    render() {
        const { scriplet, closeNavMenu, onEdit, onDelete } = this.props;
        const { isHovered } = this.state;

        return (
            <div className="flex flex-row mb-1 px-4 py-2 rounded-md hover:bg-gray-900"
                    onMouseOver={() => {
                        this.setState({isHovered: true});
                    }}
                    onMouseOut={() => {
                        this.setState({isHovered: false});
                    }}>
                <Link onClick={closeNavMenu} 
                        to={"/scriplet/" + scriplet.id} 
                        className="w-full flex items-center text-gray-100">
                    <FontAwesomeIcon className="mr-4 text-lg text-gray-600" icon="folder" />
                    {scriplet.name}
                </Link>
                {/* TODO: Possibly remove the isHovered here since the dropdown also vanishes when unhovering */}
                <div className={`${isHovered ? "" : "hidden"}`}>
                    <DropdownButton onEdit={() => onEdit(scriplet.id)}
                                    onDelete={() => onDelete(scriplet.id)} />
                </div>
            </div>
        );
    }
}